    // progress bar   
    .container{
        width: 100%;
        position: relative;
        z-index: 1;
      }
      .progressbar li{
        float: left;
        width: 20%;
        position: relative;
        text-align: center;
      }
      .progressbar{
        counter-reset: step;
      }
      .progressbar li:before{
        content:counter(step);
        counter-increment: step;
        width: 30px;
        height: 30px;
        border: 2px solid #bebebe;
        display: block;
        margin: 0 auto 10px auto;
        border-radius: 50%;
        line-height: 27px;
        background: white;
        color: #bebebe;
        text-align: center;
        font-weight: bold;
      }
      .progressbar li:after{
        content: '';
        position: absolute;
        width:100%;
        height: 3px;
        background: #979797;
        top: 15px;
        left: -50%;
        z-index: -1;
      }
      .progressbar li:first-child:after{
        content: none;
        }
      .progressbar li:first-child:after {
         content: none;
     }
    .progressbar li.active {
        color: green;
        font-weight: bold;  
    }
    .progressbar li.active:before {
        border-color: #55b776;
        background: green;
    }
    .progressbar li.active + li:after {
        background-color: #55b776;
    }
    #progressbar{
        display: none;
    }
       
    // end progress bar

    // modal position and style
    .modal-dialog {
        transform: translate(0,0);
        position: absolute;
        top: 50%;
        left: 50%;
